footer[role='contentinfo'] {
  @include pad($base-spacing 0);
  @include ie-clearfix;

  background-color: $non-white;
  border-top: 6px solid $border-contrast-colour;

  ul {
    margin-bottom: ($base-spacing * 1.5);
    padding: 0;
  }

  li {
    list-style: none;
  }

  .feedback {
    display: block;
    margin-bottom: $base-spacing;

    @include media($tablet) {
      display: none;
    }
  }

  .button--feedback {
    display: block;
    margin: auto;
    text-align: center;
    max-width: 30em;
    background-color: transparent;
  }

  .footer-logo {
    @include media($tablet) {
      @include span-columns(3 of 12);
    }

    margin-bottom: $base-spacing * 2;

    img {
      max-height: 7em; //was $base-spacing * 4;
      display: block;
      margin: 0 auto;

      @include media($tablet) {
        margin: 0;
      }
    }
  }

  .footer-links {
    text-align: center;
    margin: 0 auto;

    @include media($tablet) {
      @include span-columns(9 of 12);

      margin: 0;
      text-align: left;
    }

    ul {
      @include media($tablet) {
        margin-top: 0;
      }
    }

    li {
      display: block;
      margin-right: $small-spacing;

      @include media($tablet) {
        display: inline-block;
      }
    }

    a {
      display: inline-block;
    }
  }

  // Footer navigation is documented in the navigation partial.
  .footer-top {
    margin-bottom: $base-spacing * 2;
    padding-bottom: $base-spacing * 2;
    border-bottom: 1px solid $border-colour;

    nav {
      @extend %base-vertical-list;

      .footer-menu {
        @include media($tablet) {
          @include span-columns(6 of 12);
          @include omega(2n);
        }

        @include media($desktop) {
          @include omega-reset(2n);
          @include span-columns(3 of 12);
          @include omega(5n);

          @include ie-lte(8) {
            width: 21%;
          }
        }

        h1,
        h2,
        h3,
        h4 {
          font-size: rem(16);
          font-weight: $heading-font-weight;
        }

        li {
          line-height: 1.5em;
          list-style: none;
        }
      }
    }
  }
}
